import React from "react";
import logo from "../../../assets/logo.JPG";

const Terms_Service = () => {
  return (
    <div className="mt-4 p-4 d-flex justify-content-center flex-column align-items-center">
      <img src={logo} alt="" width="50px" height="50px" />
      <p
        className="
        mt-3 "
        style={{ textAlign: "center", fontSize: "0.8rem" }}
      >
        Lorem ipsum dolor sit amet consectetur adipisicing elit. Quod a, minus
        blanditiis culpa dolorem cum nulla aperiam facere repudiandae minima.
        Recusandae quod, veritatis minima commodi laudantium quasi vero?
        Consequatur, corrupti?
      </p>
    </div>
  );
};

export default Terms_Service;
